@import '../../variables'

.homecontact
    background: url(../../assets/img/homecontact-bg.png) center (center/cover) no-repeat
    @include centering
    flex-direction: column
    padding: 10% 0
    &_head
        width: 40%
        padding-bottom: 3%
        position: relative
        h2
            text-align: center
            font-size: 4.5rem
            font-weight: 900
            line-height: 5.125rem
        img
            position: absolute
            top: 75%
            right: 0
    &_desc
        width: 40%
        color: #555
        font-size: 1.125rem
        line-height: 1.75rem
        padding-bottom: 3%
        text-align: center
    &_form
        width: 50%
        form
            width: 100%
            display: flex
            justify-content: center
            height: 80px
            input
                width: 67%
                font-size: 1.125rem
                border: none
                padding: 0 5%
                outline: none
            button
                color: $textColor
                background-color: $mainColor
                width: 33%

                text-align: center
                font-size: 1.125rem
                font-weight: 700
                border: none
                height: 100%
